:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;

}

.btn--primary {
    background-color: transparent;
    color: #fff;
    border: 1px solid rgb(255, 0, 136);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-in-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 46px;
    font-size: 20px;
}

.btn--medium:hover, 
.btn--large:hover {
    background: #fff;
    color: #242424;
    transition: all 0.3s ease-in-out;
}